



































import VueGrid     from '../VueGrid/VueGrid.vue';
import VueGridItem from '../VueGridItem/VueGridItem.vue';
import VueGridRow  from '../VueGridRow/VueGridRow.vue';
import VueCollapse from '../VueCollapse/VueCollapse.vue';
import { EventBus } from '../../services/EventBus';

export default {
  name:       'VueNavBar',
  components: {
    VueGrid,
    VueGridItem,
    VueCollapse,
    VueGridRow,
  },
  data() {
    return {
      isSticky: false,
      isOpen:   false,
    };
  },
  props:      {
    to:       {
      type:     String,
      required: false,
      default:  '/',
    },
    imageUrl: {
      type:     String,
      required: false,
      default:  '/logo.png',
    },
  },
  computed:   {
    bgCssClasses() {
      const classes = [this.$style.bg];

      if (this.isSticky || this.isOpen) {
        classes.push(this.$style.in);
      }

      return classes;
    },
    hamburgerCssClasses() {
      const classes = [this.$style.hamburger];

      if (this.isSticky || this.isOpen) {
        classes.push(this.$style.in);
      }

      if (this.isOpen) {
        classes.push(this.$style.active);
      }

      return classes;
    },
  },
  methods:    {
    handleScroll() {
      const scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0;
      this.isSticky = scrollTop > 0;
    },
    handleDocumentClick(e: Event) {
      if (this.$refs.nav.contains(e.target) === false) {
        this.isOpen = false;
      }
    },
    handleCloseEvent() {
      this.isOpen = false;
    }
  },
  beforeMount() {
    window.addEventListener('scroll', this.handleScroll);
    document.addEventListener('click', this.handleDocumentClick);
    document.addEventListener('touchstart', this.handleDocumentClick);
    EventBus.$on('navbar.close', this.handleCloseEvent);
  },
  mounted() {
    this.handleScroll();
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll);
    document.removeEventListener('click', this.handleDocumentClick);
    document.removeEventListener('touchstart', this.handleDocumentClick);
  },
  watch:{
    $route (){
      this.isOpen = false;
    }
  },
};
